//Select

.select-list {
  position: relative;
  // display: inline-flex;
  .select-control {
    background: $selectbox-bg-color;
    height: 40px;
    padding: 0 40px 0 $md;
    font-size: $fs-16;
    border: 1px solid $selectbox-border-color;
    outline: none;
    -webkit-appearance: none;
    @include border-radius(5px);
    &:focus {
      border-color: $input-active-border-color;
		}
		&.-err {
			border-color: $input-error-border-color;
		}
  }
  &:after {
    font-family: $fonticon;
    content: $bo-arrow-down;
    font-size: $fs-14;
    position: absolute;
    top: 0;
    right: 5px;
    bottom: 0;
    line-height: calc(39px - 2px);
    pointer-events: none;
    padding: 2px $sm;
    color: $selectbox-arrow-color;
  }
  &.-medium {
    .select-control {
      height: 50px;
    }
    &:after {
      line-height: 49px;
    }
  }
}

//Textbox

.form-element {
  position: relative;

  .lookPassword {
    opacity: 0;
    position: absolute;
    right: 26px;
    top: 28px;
    font-size: $fs-14;
    cursor: default;
  }

  &.-focus .focus-input ~ .focus-label{
    font-size: $fs-12;
    top: 15px;
    color: $Emperor;
  }
  &.-focus .lookPassword {
    opacity: 1;
  }
	.focus-input,
	.focus-textarea {
    border: solid 1px $input-border-color;
    border-radius: 5px;
    height: 70px;
    padding: 15px $lg 0 $lg;
    font-size: $fs-18;
    width: 100%;

    ~ .focus-label {
      position: absolute;
      transition: 0.2s cubic-bezier(0.77, 0, 0.175, 1);
      left: 26px;
			// top: calc(50% - 9px);
			top: 26px;
      font-size: $fs-16;
    }
    &:focus ~ .focus-label {
      font-size: $fs-12;
      top: 15px;
      color: $Emperor;
    }
    &:focus {
      border-color: $input-active-border-color;
    }
	}
	.focus-textarea {
		resize: none;
		height: 100px;
	}
}

.form-control {
  background: $input-bg-color;
  color: $input-text-color;
  height: 40px;
  font-size: $fs-16;
  font-weight: $regular;
  border: 1px solid $input-border-color;
  outline: none;
  -webkit-appearance: none;
  @include border-radius(5px);
  &:focus {
    border-color: $input-active-border-color;
  }
  &.-err {
    border-color: $input-error-border-color;
  }
  &.-small {
    padding: $xs $md;
  }
  &.-cvv {
    width: 110px;
  }
}	

.-errMsg {
	color: $input-error-border-color;
	margin: $xs 0;
}

//Radio Button

.custom-radio {
  position: absolute;
  opacity: 0;
  display: none;
  & ~ .custom-radio-label {
    position: relative;
    padding-left: 30px;
    cursor: pointer;
    display: flex;
    align-items: center;
    line-height: 20px;
    height: 20px;
    &:after {
      content: "";
      background: $White;
      position: absolute;
      left: 0;
      top: 0;
      width: 20px;
      height: 20px;
      border-radius: 50%;
      border: 1px solid $DustyGray;
    }
  }
  &:checked {
    & ~ .custom-radio-label {
      font-weight: $bold;

      &:after {
        border: 1px solid $Niagara;
      }
      &:before {
        content: "";
        background: $Niagara;
        border-radius: 50%;
        width: 14px;
        height: 14px;
        position: absolute;
        z-index: 1;
        left: 3px;
        top: 3px;
      }
    }
  }
}

//Checkbox
.custom-checkbox {
  opacity: 0;
  display: none;
  & ~ .custom-checkbox-label {
    position: relative;
    padding-left: 30px;
    cursor: pointer;
    display: flex;
    align-items: center;
    line-height: 1.5;

    &:after {
      content: "";
      background: $White;
      position: absolute;
      top: 0;
      left: 0;
      width: 20px;
      height: 20px;
      border-radius: 2px;
      border: 1px solid $DustyGray;
    }
  }
  &:checked {
    &  ~ .custom-checkbox-label {
      &:after {
        border: 1px solid $Niagara;
        background: $Niagara;
        font-family: $fonticon !important;
        content: $bo-check;
        color: $White;
        font-size: $fs-10;
        padding: 3px 4px;
      }
    }
  }
}

//genel formlar için
.form-row {
  margin-bottom: $lg;
  display: flex;
  align-items: center;

  .ml-20 {
    margin-left: 20px;
  }
  .label {
    font-size: $fs-16;
    color: $Shark;
  }
}

//Login, register için
.form-rows {
  display: flex;
  flex-wrap: wrap;
  margin-right: -5px;
  margin-left: -5px;
  .form-group {
    margin-bottom: 10px;
  }
  .form-control,
  .select-control {
    width: 100%;
  }
  & > col,
  & > [class*="col-"] {
    padding-right: 5px;
    padding-left: 5px;
  }
  .-agreement {
    font-size: $fs-14;
    color: $Emperor;
  }
  
  .or {
    text-align: center;
    position: relative;
    width: 100%;

    &:before {
      content: "";
      background: $FrenchGray;
      width: 100%;
      height: 1px;
      top: 50%;
      display: block;
      position: absolute;
    }
    span {
      background: $White;
      display: inline-block;
      position: relative;
      z-index: 1;
      padding: $lg $sm;
      font-size: $fs-14;
    }
  }
}

.o-formType {
  &.-classic {
    .form-row {
      flex-direction: column;
      align-content: flex-start;
      align-items: flex-start;

      label {
        display: block;
        font-weight: $bold;
      }
    }
  }
}

.input-group {
	&-prepend {
		position: absolute;
		top: 23px;
		left: 26px;
		font-size: 25px;
	}

	.form-control {
		padding-left: 65px;
	}

	.focus-label {
		left: 70px !important;
	}
}